@charset "utf-8";

.movie {
  background-color: #fbfbfb;
  padding: 20px;
  box-shadow: 0px 0px 10px #ccc;
  margin-right: 30px;
  margin-bottom: 30px;
  position: relative;
  display: flex;
  img {
    max-width: 150px;
    width:100%;
    margin-right: 30px;
    box-shadow: 0px 20px 20px -12px #8f8f8f;
    transition: 2s;
    perspective: 200px;
    &:hover {
      transform: perspective(300px) rotateY(360deg);
    }
  }
}

.movie_data {}
.movie_title, .movie_year {
  font-weight: 300;
}
.movie_title {
  font-size: 24px;
  margin-bottom: 5px;
  color:#2c2c2c;
}
// 장르, 년도 서식
.movie_genres {
  display: flex;
  flex-wrap: wrap;
  margin: 5px 0;
}
.movie_genres_txt, .movie_year {
  margin-right: 10px;
  font-size: 14px;
}