@charset "UTF-8";
body {
  margin: 0;
  padding: 0;
  font-family: "맑은 고딕", arial, sans-serif;
  font-size: 12px;
  color: #333;
  height: 100%;
  background-origin: #eff3f7;
}

ul, li {
  list-style: none;
  margin: 0;
  padding: 0;
}

@media (min-width: 768px) {
  .header {
    position: fixed;
    left: 0;
    top: 0;
    background-color: #ccc;
    width: 180px;
    height: 100vh;
  }
  .header > h1 {
    text-align: center;
    padding: 15px 0;
  }
  main {
    position: absolute;
    width: calc(100% - 180px);
    left: 180px;
    top: 0px;
    padding: 30px;
    box-sizing: border-box;
  }
  .nav {
    background: #fff;
    text-align: center;
  }
  .nav li {
    line-height: 50px;
  }
  .nav a {
    text-decoration: none;
    font-size: 16px;
    padding: 5px 10px;
    color: #333;
    text-shadow: 0px 1px;
  }
}
@media (max-width: 767px) {
  #root {
    padding-top: 100px;
  }
  .header {
    position: fixed;
    width: 100%;
    top: 0;
    box-shadow: 2px 1px 6px #000;
    background: #fff;
  }
  .header > h1 {
    text-align: center;
  }
  .article img {
    width: 100%;
    max-width: 500px;
  }
}
.load_img {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.5s;
}
.load_img.success {
  opacity: 0;
  visibility: hidden;
}/*# sourceMappingURL=Home.css.map */